.filter-box{position:relative;padding:0px 15px;clear:both;width:100%}.filter-box input{text-align:right;color:#656565;width:93%}.filter-box label{color:black;text-align:right;right:35px}.filter-box .filter-search{height:35px;position:relative;border-radius:5px;border:1px solid #e4e4e4;padding:5px}.filter-box .filter-search .placeholder{-webkit-user-select:none;-ms-user-select:none;user-select:none;visibility:visible;position:absolute;right:30px;direction:rtl;color:#bbb}.filter-box .filter-search:focus-within{box-shadow:0px 0px 4px 2px #f3eded}.filter-box .filter-search:focus-within .placeholder{visibility:hidden}.filter-box .filter-search input{direction:rtl;position:absolute;right:30px;width:85%;top:50%;transform:translateY(-50%);border:none;font-size:19px}.filter-box .filter-search input:focus{outline:none}.filter-box .filter-search .search-icon{position:absolute;top:50%;transform:translateY(-50%);right:7px}.filter-box .filter-check{cursor:pointer;position:relative;direction:rtl;float:right;vertical-align:middle;padding:5px 10px}.filter-box .filter-check span{padding:0px 5px}.custom-checkbox{position:relative;cursor:pointer;padding:0;-webkit-user-select:none;-ms-user-select:none;user-select:none}.custom-checkbox.disabled{color:#b8b8b8;cursor:default}.custom-checkbox.disabled span{box-shadow:none;background:#ddd}.custom-checkbox span{position:relative;display:inline-block;margin-left:9px;width:20px;height:20px;border-radius:5px;background:#d4d4d4;vertical-align:middle}.custom-checkbox input{position:absolute;opacity:0}.custom-checkbox input:focus+span{box-shadow:0 0 0 3px rgba(0,0,0,0.12)}.custom-checkbox input:checked+span{background:#be901e}.custom-checkbox input:checked+span:after{content:'';position:absolute;left:30%;top:50%;background:#fff;width:2px;height:2px;box-shadow:2px 0 0 #fff,4px 0 0 #fff,4px -2px 0 #fff,4px -4px 0 #fff,4px -6px 0 #fff,4px -8px 0 #fff;transform:rotate(45deg) translate(-50%, -50%)}@media only screen and (min-width: 650px){.filter-box .filter-search{width:100%;float:right;max-width:300px}}
